import { Trans } from '@lingui/react'
import { Dialog, DialogActions, DialogContent, DialogTitle } from '@material-ui/core'
import { IOrderControlSidedishProps, SidedishPicker } from 'c2e-tools'
import React from 'react'
import styled from 'styled-components'

import { ScrollArea } from '../../../atoms/ScrollArea'
import { i18n } from '../../../i18n'
import { TouchButton } from '../../../molecules/TouchButton'
import { Colors } from '../../../styling'
import { OrderMenuSidedishButton } from '../atoms/OrderMenuSidedishButton'

interface IProps extends IOrderControlSidedishProps {}

const SidedishCategory = styled.div`
  font-size: 1rem;
  font-weight: bold;
  text-align: left;
  margin: 0 0 0.2rem 0.2rem;
  color: ${Colors.black};
`

export const OrderSidedishModal: React.SFC<IProps> = props => (
  <Dialog open={true} onClose={props.onCancel} scroll="body" maxWidth="lg">
    <DialogTitle>
      {props.recipe ? (
        <Trans>{`Add something else to ${props.recipe.nameLabel}?`}</Trans>
      ) : (
        <Trans>Loading sidedishes...</Trans>
      )}
    </DialogTitle>
    {props.recipe && (
      <DialogContent>
        <ScrollArea>
          <SidedishPicker
            recipe={props.recipe}
            formatCategory={category => (
              <SidedishCategory>{category.nameLabel}</SidedishCategory>
            )}
            formatSidedish={sidedish => (
              <OrderMenuSidedishButton
                label={sidedish.nameLabel}
                isSelected={props.isSidedishSelected(sidedish.id)}
                onClick={() => props.onSidedishToggle(sidedish.id)}
              />
            )}
          />
        </ScrollArea>
      </DialogContent>
    )}
    {props.recipe && (
      <DialogActions>
        <TouchButton text={i18n.t`Cancel`} onClick={props.onCancel} />
        <TouchButton
          text={i18n.t`Confirm`}
          onClick={props.onConfirm}
          color={Colors.green}
        />
      </DialogActions>
    )}
  </Dialog>
)
