import { Observer } from 'mobx-react'
import React from 'react'

import { OrderCreateControl } from '../atoms/OrderCreateControl'
import { OrderMenuList } from '../molecules/OrderMenuList'
import { OrderMenuNavigator } from '../molecules/OrderMenuNavigator'
import { QueryOrderMenu } from './OrderMenu.graphql'

interface IProps {}

export const OrderMenu: React.SFC<IProps> = props => (
  <OrderCreateControl>
    {({ createOrder, onRecipeClick }) => (
      <Observer>
        {() => (
          <QueryOrderMenu
            variables={{ branchId: createOrder.branchId }}
            render={({ orderMenu }) => (
              <>
                <OrderMenuNavigator
                  orderMenu={orderMenu}
                  onClick={createOrder.scrollToCategory}
                />
                <Observer>
                  {() => (
                    <OrderMenuList
                      orderMenu={orderMenu}
                      // for animated scrolling something like this can be used
                      // https://codesandbox.io/s/jp84p8mvvy
                      scrollToCategory={createOrder.scrollMenuToCategoryId}
                      onRecipeClick={onRecipeClick}
                    />
                  )}
                </Observer>
              </>
            )}
          />
        )}
      </Observer>
    )}
  </OrderCreateControl>
)
