import { Observer } from 'mobx-react'
import React, { useEffect } from 'react'
import { Form } from 'react-form'

import { useRoot } from '../../../atoms/RootProvider'

interface IProps {
  callingPhone?: string
  callingEventId: MaybeID
}

export const OrderFormSetup: React.SFC<IProps> = ({
  callingPhone,
  callingEventId,
  children,
}) => {
  const { createOrder } = useRoot()
  useEffect(
    () => {
      if (callingPhone) {
        createOrder.userIsCalling(callingPhone, callingEventId)
      }
    },
    [callingPhone, callingEventId],
  )
  return (
    <Observer>
      {() => (
        <Form
          values={createOrder.formValues}
          onSubmit={createOrder.acceptOrder}
          onChange={formState =>
            createOrder.updateFormValues(formState.values, formState.touched)
          }
        >
          {children}
        </Form>
      )}
    </Observer>
  )
}
