import './OrderFormDeliveryAddress.css'

import { Trans } from '@lingui/react'
import { Warning } from '@material-ui/icons'
import React, { Component } from 'react'
import Geosuggest, { Suggest } from 'react-geosuggest'
import styled from 'styled-components'

import { FlexRowSingle } from '../../../atoms/FlexRowSingle'
import { RootContext } from '../../../context/RootContext'
import { GOrderVam } from '../../../graph/types'
import { i18n } from '../../../i18n'
import { TRootModel } from '../../../models/RootModel'
import { Colors, Spacing } from '../../../styling'
import { OrderFormLocationButton } from '../atoms/OrderFormLocationButton'

interface IProps {
  isVisible: boolean
}

const InputLine = styled(FlexRowSingle)`
  justify-content: flex-start;
`

const GeosuggestThemed = styled(Geosuggest)`
  & .geosuggest__input {
    width: 20rem;
    outline: none;
    margin: ${Spacing.MarginAroundFormComponents};
    padding: 0.5rem;
    box-shadow: none;
    border: 1px solid ${Colors.greyMiddle};
  }
`

const ZoneWarning = styled.div`
  display: inline-flex;
  align-items: center;
  color: ${Colors.violet};
`

export class OrderFormDeliveryAddress extends Component<IProps> {
  inputRef = React.createRef<Geosuggest>()
  static contextType = RootContext
  get root() {
    return this.context as TRootModel
  }
  componentDidUpdate() {
    const { createOrder } = this.root
    if (createOrder.vamCustomer !== null) {
      const { locations } = createOrder.vamCustomer
      if (locations.length === 1) {
        this.useLocation(locations[0])
      }
    }
    if (
      !createOrder.location &&
      createOrder.fakeAddress &&
      this.inputRef.current
    ) {
      // @ts-ignore Suggest need additional fields which are redundant here
      this.inputRef.current.selectSuggest({ label: createOrder.fakeAddress })
    }
  }
  onSuggestSelect = (suggest: Suggest) => {
    if (suggest) {
      const { lat, lng } = suggest.location
      this.root.createOrder.useLocation({
        latitude: Number(lat),
        longitude: Number(lng),
      })
    }
  }
  onLocationButtonClick = (location: GOrderVam.Locations) => {
    this.useLocation(location)
  }
  private useLocation(location: GOrderVam.Locations) {
    if (this.inputRef.current) {
      this.inputRef.current.update(
        `${location.street} ${location.houseNumber}, ${location.city}`,
      )
    }
    this.root.createOrder.useLocation(location.gps!)
  }
  render() {
    const {
      createOrder,
      user: { currentBranch: branch },
    } = this.root
    const location = branch
      ? new google.maps.LatLng(branch.gps.latitude, branch.gps.longitude)
      : undefined
    return (
      <div hidden={!this.props.isVisible}>
        <InputLine>
          <GeosuggestThemed
            ref={this.inputRef}
            placeholder={i18n.t`Deliver to address`.toLocaleLowerCase()}
            location={location}
            radius={50 * 1000}
            minLength={3}
            types={['geocode']}
            onSuggestSelect={this.onSuggestSelect}
          />
          {createOrder.notDeliveryLocation && (
            <ZoneWarning>
              <Warning />
              <Trans>Address is not within any delivery zone</Trans>
            </ZoneWarning>
          )}
        </InputLine>
        {createOrder.hasMultipleVamLocations &&
          createOrder.vamCustomer!.locations.map((loc, idx) => (
            <OrderFormLocationButton
              key={idx}
              location={loc}
              onClick={() => this.onLocationButtonClick(loc)}
            />
          ))}
      </div>
    )
  }
}
