import React from 'react'

import { GOrderVam } from '../../../graph/types'
import { TouchButton } from '../../../molecules/TouchButton'

interface IProps {
  location: GOrderVam.Locations
  onClick(gps: GeoPoint): void
}

export const OrderFormLocationButton: React.SFC<IProps> = ({
  location: { street, houseNumber, city, gps },
  onClick,
}) => (
  <TouchButton onClick={() => onClick(gps!)} width="15rem" height="3rem">
    {`${street} ${houseNumber}`}
    <br />
    {city}
  </TouchButton>
)
