import { CircularProgress } from '@material-ui/core'
import { Check, Close, WarningOutlined } from '@material-ui/icons'
import faker from 'faker'
import React from 'react'
import styled from 'styled-components'

import { useRoot } from '../../../atoms/RootProvider'
import { GOrderVam } from '../../../graph/types'
import { reportError, reportFailingQuery } from '../../../helpers/sentry'
import { TRootModel } from '../../../models/RootModel'
import { Colors } from '../../../styling'
import { OrderVamQuery } from './OrderFormCustomerLookup.graphql'

interface IProps {
  phone?: string
  branchId: ID
}

interface IState {
  statusIcon: ReactNode
}

const ErrorIcon = styled(WarningOutlined)`
  font-size: 1rem;
  color: ${Colors.orange};
`

const NotFoundIcon = styled(Close)`
  font-size: 1rem;
  color: ${Colors.greyMiddle};
`

const FoundIcon = styled(Check)`
  font-size: 1rem;
  color: ${Colors.green};
`

export default function OrderFormCustomerLookupConnected() {
  const root = useRoot()
  return (
    <OrderFormCustomerLookup
      root={root}
      branchId={root.createOrder.branchId}
      phone={root.createOrder.phone}
    />
  )
}

class OrderFormCustomerLookup extends React.PureComponent<
  IProps & { root: TRootModel },
  IState
> {
  state: IState = { statusIcon: null }
  componentDidMount() {
    this.executeQuery()
  }
  componentDidUpdate(prevProps: IProps) {
    const { phone, branchId } = this.props
    if (!(phone === prevProps.phone && branchId === prevProps.branchId)) {
      this.executeQuery()
    }
  }
  async executeQuery() {
    const { phone, root } = this.props
    const { client, createOrder } = root!
    if (!isPhoneValid(phone)) {
      createOrder.updateFromVam(null)
      return this.setStatus(null)
    }

    this.setStatus(<CircularProgress size={20} />)

    const variables = {
      branchId: createOrder.branchId,
      phone: phone,
    }

    try {
      const { data, errors } = await client.query<
        GOrderVam.Query,
        GOrderVam.Variables
      >({ query: OrderVamQuery, variables })

      if (errors) {
        reportFailingQuery(errors, {
          query: 'OrderVam',
          variables,
        })
        return this.setStatus(<ErrorIcon />)
      }

      const { vamCustomer } = data
      createOrder.updateFromVam(vamCustomer)
      this.setStatus(vamCustomer.isFound ? <FoundIcon /> : <NotFoundIcon />)
    } catch (err) {
      reportError(err)
      this.setStatus(<ErrorIcon />)
    }
  }
  setStatus(statusIcon: ReactNode) {
    this.setState({ statusIcon })
  }
  render() {
    return this.state.statusIcon
  }
}

function isPhoneValid(phone?: string): phone is string {
  return Boolean(phone && phone.length > 6)
}

// @ts-ignore Can be useful when there is an error in DEV
function getFakeVam(): GOrderVam.VamInformation {
  return {
    firstName: 'FAKE',
    lastName: faker.name.lastName(),
    email: faker.internet.email(),
    lastOrder: {
      note: faker.lorem.sentence().slice(0, 50 - 1),
    },
    isFound: true,
    locations: [],
  }
}
