import { IOrderControlContext, OrderControl } from 'c2e-tools'
import React from 'react'

import { WithRoot } from '../../../atoms/WithRoot'
import { TCreateOrderModel } from '../models/CreateOrderModel'
import { OrderSidedishModal } from '../molecules/OrderSidedishModal'

interface IProps {
  children(
    props: {
      createOrder: TCreateOrderModel
      branchId: ID
    } & IOrderControlContext,
  ): ReactNode
}

export const OrderCreateControl: React.SFC<IProps> = ({ children }) => (
  <WithRoot
    render={({ createOrder, user }) => (
      <OrderControl
        apolloClient={createOrder.client}
        getOrderId={createOrder.ensureOrder}
        sidedishPickerComponent={OrderSidedishModal}
      >
        {context =>
          children({ ...context, branchId: user.currentBranchId!, createOrder })
        }
      </OrderControl>
    )}
  />
)
