import { Trans } from '@lingui/react'
import { OrderCartLoader } from 'c2e-tools'
import { Observer } from 'mobx-react'
import React from 'react'
import styled from 'styled-components'

import { VerticalGap } from '../../../atoms/VerticalGap'
import { OrderCreateControl } from '../atoms/OrderCreateControl'
import { OrderCartItem } from '../molecules/OrderCartItem'
import { OrderCartTotals } from '../molecules/OrderCartTotals'

interface IOrderCartProps {}

const CartStyled = styled.div`
  margin: 0.1rem 0.1rem 0.1rem 0;
`
const EmptyCartStyled = styled.div`
  margin: 0.5rem 0 0 0;
`

export const OrderCart: React.SFC<IOrderCartProps> = props => (
  <OrderCreateControl>
    {({ createOrder, onItemRemove, onItemEdit }) => (
      <Observer>
        {() => (
          <OrderCartLoader
            key={createOrder.cartRefreshKey}
            orderId={createOrder.orderId}
            renderEmpty={() => (
              <EmptyCartStyled>
                <Trans>Cart is empty</Trans>
              </EmptyCartStyled>
            )}
            render={order => (
              <CartStyled>
                <OrderCartTotals order={order} />
                <VerticalGap />
                {order.items.map(item => (
                  <OrderCartItem
                    key={item.id}
                    item={item}
                    onEdit={() => onItemEdit(item.id)}
                    onRemove={() => onItemRemove(item.id)}
                  />
                ))}
              </CartStyled>
            )}
          />
        )}
      </Observer>
    )}
  </OrderCreateControl>
)
