import React from 'react'

import { GAddress } from '../../../graph/types'

interface IAddressDisplayProps {
  address: GAddress.Fragment | null
}

export const AddressDisplay: React.SFC<IAddressDisplayProps> = ({ address }) =>
  address ? (
    <span>{`${address.street} ${address.houseNumber}, ${address.city}`}</span>
  ) : (
    <i>unknown</i>
  )
